/* Container */
.container {
  width: 100%;
  max-width: 90%;
  /* Adjusted max-width for better scalability */
  margin: 0 auto;
  padding: 5vw;
  /* Using viewport units for padding */
  box-sizing: border-box;
}

/* Header */
.header {
  text-align: center;
  margin-bottom: 6vw;
  /* Adjusted margin for better scalability */
}

.title {
  font-size: 4.8vw;
  /* Adjusted font size for better scalability */
  color: #333;
  font-weight: bold;
  text-transform: uppercase;
}

/* Content */
.content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Task Section */
.task-section {
  width: 100%;
  max-width: 90%;
  /* Adjusted max-width for better scalability */
  margin-top: 8vw;
  /* Adjusted margin for better scalability */
}

.section-title {
  font-size: 3.6vw;
  /* Adjusted font size for better scalability */
  margin-bottom: 4vw;
  /* Adjusted margin for better scalability */
  color: #333;
  font-weight: bold;
  text-transform: capitalize;
}

/* Tool */
.tool {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 6vw;
  /* Adjusted margin for better scalability */
}

.tool input[type="text"] {
  width: calc(100% - 20vw);
  /* Adjusted width for better scalability */
  padding: 3vw;
  /* Using viewport units for padding */
  margin-right: 4vw;
  /* Adjusted margin for better scalability */
  border: 0.2vw solid #ccc;
  /* Adjusted border for better scalability */
  border-radius: 2vw;
  /* Adjusted border-radius for better scalability */
  font-size: 2vw;
  /* Adjusted font size for better scalability */
}

.tool button {
  background-color: #007bff;
  color: #fff;
  padding: 3vw 6vw;
  /* Using viewport units for padding */
  border: none;
  border-radius: 2vw;
  /* Adjusted border-radius for better scalability */
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 2vw;
  /* Adjusted font size for better scalability */
  margin-right: 4vw;
  /* Adjusted margin for better scalability */
  margin-bottom: 4vw;
  /* Adjusted margin for better scalability */
}

/* Filter */
.filter {
  margin-top: 6vw;
  /* Adjusted margin for better scalability */
  text-align: center;
}

.filter span {
  font-size: 2.4vw;
  /* Adjusted font size for better scalability */
  margin-right: 4vw;
  /* Adjusted margin for better scalability */
}

/* Task List */
.task-list {
  list-style: none;
  padding: 0;
  width: 100%;
}

.task-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 4vw;
  border-bottom: 0.2vw solid #eee;
  /* Adjusted border for better scalability */
  cursor: pointer;
}

.task-item.completed {
  text-decoration: line-through;
  color: #999;
}

.task-item span {
  font-size: 2vw;
  /* Adjusted font size for better scalability */
}

.task-item button {
  background-color: #ff4444;
  color: #fff;
  padding: 1.2vw 2.4vw;
  /* Using viewport units for padding */
  border: none;
  border-radius: 2vw;
  /* Adjusted border-radius for better scalability */
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 2vw;
  /* Adjusted font size for better scalability */
}

.task-item button:hover {
  background-color: #cc0000;
}

/* Footer */
.footer {
  margin-top: 6vw;
  /* Adjusted margin for better scalability */
  text-align: center;
  color: #777;
}